@import "./../../common/styles/sass/variables";
@import "./../../common/styles/sass/mixins";

.project {
  /*!*dlya dvuh kolonok*!*/
  background-color: $backgroundColorPrimary;
  max-width: 650px;
  min-width: 320px;
  height: 360px;
  width: 33%;
  margin-top: 5px;
  margin-bottom: 40px;

  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  .dws {
    position: relative;
    width: 90%;
    height: 300px;

    .blocImg {
      margin-top: 10px;
      width: 100%;
      height: 300px;
      overflow: hidden;
      background-color: $backgroundColorPrimary;

      .blocText {
        position: absolute;
        top: 240px;
        left: 10px;
        right: 10px;
        bottom: 10px;
        transition: .35s;


        &::before,
        &::after {
          content: '';
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          transition: .7s;
        }

        &::before {
          border-bottom: 1px solid $textColor;
          transform: scale(0, 1);
        }


      }

      & img {
        width: 100%;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        transition: .7s;
      }

    }

    .div {
      @include text($buttonFontFamily, 400, 16px, $textColor);

      margin-top: 10px;
      margin-bottom: 10px;
      width: 90.5%;

      display: inline-block;

      background: $backgroundColorPrimary;
      padding: 4px 20px 4px 20px;
      border: solid $textColor 1px;
      text-decoration: none;
      transition: .35s;
      cursor: pointer;

      &:hover {
        border: solid $hoverTextColor 1px;
        color: $hoverTextColor;
        text-decoration: none;
      }
    }

    &:hover .blocImg img {
      transform: scale(1.2);
      filter: blur(1px);
      opacity: 0.15;
    }

    &:hover .blocText {
      top: 120px;
      bottom: 130px;
    }

    &:hover .text {
      color: $hoverTextColor;
    }

    &:hover .text span {
      color: $textColor;
      opacity: 1;
      top: 60px;
    }

    &:hover .blocText::before,
    &:hover .blocText::after {
      transform: scale(1, 1);
    }

  }

  @media screen and (min-width: 1114px) and (max-width: 1894px) {
    max-width: 600px;
    min-width: 300px;
    width: 48%;
  }
  @media screen and (max-width: 1113px) {
    /*!*dlya odnoi kolonki*!*/
    max-width: 600px;
    min-width: 300px;
    width: 100%;
  }
}


.text {

  @include text($buttonFontFamily, 400, 16px, $textColor);


  text-transform: uppercase;
  padding: 0 20px;
  opacity: 1;
  transition: .35s;
}

.text h3 {
  font-size: 22px;
}

.text span {
  text-transform: none;
  font-size: 16px;
  opacity: 0;
  transition: .35s;

  position: absolute;
  top: 0px;
  left: 10px;
  right: 10px;
  bottom: 10px;
}


